const _import = require('./_import_' + process.env.NODE_ENV)

Vue.use(Router)

export const constantRouterMap = [
  {
    path: '',
    component: Layout,
    redirect: '',
    meta: {
      title: 'General',
      icon: 'general'
    },
    hidden: false },
  { path: '/login', component: _import('login/index'), hidden: true },
  { path: '/Entranceguardmonitoring', component: _import('Entranceguardmonitoring/index'), hidden: true },
  { path: '/Vehiclemonitoring', component: _import('Vehiclemonitoring/index'), hidden: true },
  { path: '/Personnelpositioning', component: _import('Personnelpositioning/index'), hidden: true },
  { path: '/Videomonitoring', component: _import('Videomonitoring/index'), hidden: true },
  { path: '/Accountinformation', component: _import('Accountinformation/index'), hidden: true }
]
export default new Router({
  mode: 'hash',
  routes: constantRouterMap
})

export const asyncRouterMap = [
]



