<template>
  <div class="app-wrapper" :class="classObj">
    <div style="height: 100%; color: #dfdfdf;" class="main-container">
      <el-container style="height: 100%; backgroud: #dfdfdf;">
        <div :style="{height: specHeight + 'px'}" style="width: 100%;"></div>
        <el-header style="padding: 0px; height: 40px">
          <mt-header title="山海高速智慧工地云平台"></mt-header>
          <!-- 信息化集成平台 -->
        </el-header>
        <el-main style="height: 80%; text-align: center">
          <el-row :gutter="5" style="margin-bottom: 5px; height: 32%; text-align: center" type="flex" class="row-bg">
            <el-col style="height: 100%; text-align: center" :span="12">
              <el-card shadow="always" :body-style="{ height: '100%', center: true }" @click.native="clickHandle(1)" style="color: #323232; height: 45%; margin-bottom: 5px;" class="box-card">
                <img class="title-image" :src="tranlatePath('static/img/guard.png')" width="45">
                <span class="classtitle">门禁监控</span>
              </el-card>
              <el-card shadow="always" :body-style="{ height: '100%', center: true }" @click.native="clickHandle(2)" style="color: #323232; height: 51%; margin-bottom: 0px;" class="box-card">
                <img class="title-image" :src="tranlatePath('static/img/car.png')" width="45">
                <span class="classtitle">车辆监控</span>
              </el-card>
            </el-col>
            <el-col style="height: 100%; text-align: center" :span="12">
              <el-card shadow="always" :body-style="{ height: '100%', center: true }" @click.native="clickHandle(5)" style="color: #323232; height: 100%;  margin-bottom: 0px;" class="box-card">
                <img style="margin-top: 25%;" :src="tranlatePath('static/img/vedio.png')" width="75">
                <br/>
                <span>视频监控</span>
              </el-card>
            </el-col>
          </el-row>
          <el-row :gutter="5" style="margin-bottom: 5px; height: 16%;" type="flex" class="row-bg">
            <el-col style="height: 100%;" :span="12">
              <el-card shadow="always" :body-style="{ height: '100%', center: true }" @click.native="clickHandle(3)" style="color: #323232; height: 100%; text-align: center" class="box-card">
                <img class="title-image" :src="tranlatePath('static/img/position.png')" width="45">
                <span class="classtitle">人员定位监控</span>
              </el-card>
            </el-col>
            <el-col style="height: 100%;" :span="12">
              <el-card shadow="always" :body-style="{ height: '100%' }" @click.native="clickHandle(6)" style="color: #323232; height: 100%; text-align: center" class="box-card">
                <img class="title-image" :src="tranlatePath('static/img/secury.png')" width="45">
                <span class="classtitle">安全巡查反馈</span>
              </el-card>
            </el-col>
          </el-row>
          <el-row :gutter="5" style="margin-bottom: 5px; height: 16%;" type="flex" class="row-bg">
            <el-col style="height: 100%;" :span="12">
              <el-card shadow="always" :body-style="{ height: '100%' }" @click.native="clickHandle(7)" style="color: #323232; height: 100%; text-align: center" class="box-card">
                <img class="title-image" :src="tranlatePath('static/img/stellwidth.png')" width="45">
                <span class="classtitle">钢筋保护层厚度检测</span>
              </el-card>
            </el-col>
            <el-col style="height: 100%;" :span="12">
              <el-card shadow="always" :body-style="{ height: '100%' }" @click.native="clickHandle(8)" style="color: #323232; height: 100%; text-align: center" class="box-card">
                <img class="title-image" :src="tranlatePath('static/img/monitor.png')" width="45">
                <span class="classtitle">物联监测</span>
              </el-card>
            </el-col>
          </el-row>
          <el-row :gutter="5" style="margin-bottom: 5px; height: 16%;" type="flex" class="row-bg">
            <el-col style="height: 100%;" :span="12">
              <el-card shadow="always" :body-style="{ height: '100%' }" @click.native="clickHandle(9)" style="color: #323232;  height: 100%; text-align: center" class="box-card">
                <img class="title-image" :src="tranlatePath('static/img/cloud.png')" width="45">
                <span class="classtitle">空气质量和声环境监测</span>
              </el-card>
            </el-col>
            <el-col style="height: 100%;" :span="12">
              <el-card shadow="always" :body-style="{ height: '100%' }" @click.native="clickHandle(10)" style="color: #323232; height: 100%; text-align: center" class="box-card">
                <img class="title-image" :src="tranlatePath('static/img/grind.png')" width="45">
                <span class="classtitle">摊铺碾压</span>
              </el-card>
            </el-col>
          </el-row>
          <el-row :gutter="5" style="margin-bottom: 5px; height: 16%;" type="flex" class="row-bg">
            <el-col style="height: 100%;" :span="12">
              <el-card shadow="always" :body-style="{ height: '100%' }" @click.native="clickHandle(11)" style="color: #323232; height: 100%; text-align: center" class="box-card">
                <img class="title-image" :src="tranlatePath('static/img/gas.png')" width="45">
                <span class="classtitle">隧道内气体检测</span>
              </el-card>
            </el-col>
            <el-col style="height: 100%;" :span="12">
              <el-card shadow="always" :body-style="{ height: '100%' }" @click.native="clickHandle(4)" style="color: #323232; height: 100%; text-align: center" class="box-card">
                <img class="title-image" :src="tranlatePath('static/img/accountinfo.png')" width="45">
                <span class="classtitle">账号信息</span>
              </el-card>
            </el-col>
          </el-row>
        </el-main>
        <!-- <el-footer style="height: 10%; text-align: center">深圳市宇时科技有限公司</el-footer> -->
      </el-container>
    </div>
    <el-dialog
      class="dialog-container"
      v-dialogDrag
      title="消费入单"
      ref="dialog_wrapper"
      :visible.sync="gobaldialogVisible"
      :width="dialogWidth"
      :modal-append-to-body='false'
      :close-on-click-modal="false">
        <div slot="title">
          <p style="color: #37bfc8;">{{returnDialogTitle()}}</p>
        </div>
        <div class="dialog-body">
          <component
            v-bind:is="currentTabComponent"
          ></component>
          <div
            class="line"
            v-dialogDragWidth="$refs.dialog_wrapper">
          </div>
        </div>
    </el-dialog>
  </div>
</template>
<script>
import { Navbar, Slidebar, TagsView, AppMain, UserInfo, ChangePhone, ChangePassword } from './components'
import ResizeMixin from './mixin/ResizeHandler'
import { formatPath } from '@/utils'
export default {
  name: 'layout',
  components: {
    Navbar,
    Slidebar,
    TagsView,
    AppMain,
    UserInfo,
    ChangePhone,
    ChangePassword
  },
  mixins: [ResizeMixin],
  mounted () {
    if (this.isMobile()) {
      this.dialogWidth = '100%'
    } else {
      this.dialogWidth = '40%'
    }
    var test = this.$store.getters.doorGuardList['1001']
    console.log(test)
    var that = this

    window.dataLink.start(function (msgData) {
      console.log(JSON.stringify(msgData))
      console.log('receive one message!')
      // msgData = {"TypeCode":0,"ProjectNO":1001,"DataList":[{"SourceRecordID":2,"DeptName":"机械","CPH":"粤B77551","UserName":"徐工装载机Z-3","InTime":"2018-11-10 07:11:10","OutTime":"2018-11-10 12:11:10"},{"SourceRecordID":3,"DeptName":"机械","CPH":"粤B50588","UserName":"徐工正铲装载车(Z-1)","InTime":"2018-11-10 13:45:10","OutTime":""}]}
      if (msgData.TypeCode === 0) {
        // 车辆
        if (window.yushientrypoint === 2) {
          var key1 = msgData.ProjectNO.toString()
          msgData.DataList.forEach(element => {
            if (that.$store.getters.carInfoList[key1]) {
              if (that.$store.getters.carInfoList[key1].length === 999) {
                that.$store.getters.carInfoList[key1].unshift(element)
                that.$store.getters.carInfoList[key1].pop()
              } else {
                that.$store.getters.carInfoList[key1].unshift(element)
              }
            } else {
              that.$store.getters.carInfoList[key1] = []
              that.$store.getters.carInfoList[key1].unshift(element)
            }
          })
        }
      } else if (msgData.TypeCode === 1) {
        // 门禁
        if (window.yushientrypoint === 1) {
          var key2 = msgData.ProjectNO.toString()
          msgData.DataList.forEach(element => {
            if (that.$store.getters.doorGuardList[key2]) {
              if (that.$store.getters.doorGuardList[key2].length === 999) {
                that.$store.getters.doorGuardList[key2].unshift(element)
                that.$store.getters.carInfoList[key1].pop()
              } else {
                that.$store.getters.doorGuardList[key2].unshift(element)
              }
            } else {
              that.$store.getters.doorGuardList[key2] = []
              that.$store.getters.doorGuardList[key2].unshift(element)
            }
          })
        }
      } else if (msgData.TypeCode === 2) {
        // 人员定位
        if (window.yushientrypoint === 3) {
          var key3 = msgData.ProjectNO.toString()
          msgData.DataList.forEach(element => {
            if (that.$store.getters.peoplePositionList[key3]) {
              if (that.$store.getters.peoplePositionList[key3].length === 999) {
                that.$store.getters.peoplePositionList[key3].unshift(element)
                that.$store.getters.peoplePositionList[key3].pop()
              } else {
                that.$store.getters.peoplePositionList[key3].unshift(element)
              }
            } else {
              that.$store.getters.peoplePositionList[key3] = []
              that.$store.getters.peoplePositionList[key3].unshift(element)
            }
          })
        }
      }
    }, 'http://testinfoapi.szysst.com')
  },
  data () {
    return {
      dialogWidth: '40%',
      dialogTitle: '账户信息',
      specHeight: this.$store.getters.statusBarHeight
    }
  },
  computed: {
    gobaldialogVisible: {
      get: function () {
        return this.$store.getters.dialogShow
      },
      set: function (val) {
        // this.$store.getters.dialogShow = val
        this.$store.dispatch('setDialogShow', {info: val}).then(() => {

        }).catch(() => {

        })
      }
    },
    currentTabComponent () {
      if (this.$store.getters.dialogIndex === 0) {
        return 'UserInfo'
      } else if (this.$store.getters.dialogIndex === 1) {
        return 'ChangePassword'
      } else {
        return 'ChangePhone'
      }
    },
    classObj () {
      return {
        hideSidebar: !this.slidebar.opened,
        withoutAnimation: this.slidebar.withoutAnimation,
        mobile: this.device === 'mobile'
      }
    },
    device () {
      return this.$store.getters.device
    },
    slidebar () {
      return this.$store.state.app.slidebar
    }
  },
  methods: {
    tranlatePath (path) {
      return formatPath(path)
    },
    clickHandle (type) {
      console.log(type)
      if (type === 1) {
        // 门禁监控
        window.yushientrypoint = 1
        this.$router.push({ path: '/Entranceguardmonitoring' })
      } else if (type === 2) {
        // 车辆监控
        // msgData = {"TypeCode":0,"ProjectNO":1001,"DataList":[{"SourceRecordID":2,"DeptName":"机械","CPH":"粤B77551","UserName":"徐工装载机Z-3","InTime":"2018-11-10 07:11:10","OutTime":"2018-11-10 12:11:10"},{"SourceRecordID":3,"DeptName":"机械","CPH":"粤B50588","UserName":"徐工正铲装载车(Z-1)","InTime":"2018-11-10 13:45:10","OutTime":""}]}
        // let testData4 = {'TypeCode': 0, 'ProjectNO': 1001, 'DataList': [{'SourceRecordID': 2, 'DeptName': '机械', 'CPH': '粤B77551', 'UserName': '徐工装载机Z-3', 'InTime': '2018-11-10 07:11:10', 'OutTime': '2018-11-10 12:11:10'}, {'SourceRecordID': 3, 'DeptName': '机械', 'CPH': '粤B50588', 'UserName': '徐工正铲装载车(Z-1)', 'InTime': '2018-11-10 13:45:10', 'OutTime': ''}]}
        // var keytest3 = testData4.ProjectNO.toString()
        // var that = this
        // testData4.DataList.forEach(element => {
        //   if (that.$store.getters.carInfoList[keytest3]) {
        //     that.$store.getters.carInfoList[keytest3].unshift(element)
        //   } else {
        //     that.$store.getters.carInfoList[keytest3] = []
        //     that.$store.getters.carInfoList[keytest3].unshift(element)
        //   }
        // })
        window.yushientrypoint = 2
        this.$router.push({ path: '/Vehiclemonitoring' })
      } else if (type === 3) {
        // 人员定位
        window.yushientrypoint = 3
        this.$router.push({ path: '/Personnelpositioning' })
      } else if (type === 4) {
        // 账号信息
        // let testData = {TypeCode: 1, ProjectNO: 1002, DataList: [{EmployeeName: '123', SexText: '232', EventTime: '345', TimeCoutText: '123', Event: '3433', DoorName: '4222', DeptName: 'fsd', JobName: 'skd'}]}
        // var keytest = testData.ProjectNO.toString()
        // testData.DataList.forEach(element => {
        //   this.$store.getters.doorGuardList[keytest].unshift(element)
        // })
        window.yushientrypoint = 4
        this.$router.push({ path: '/Accountinformation' })
      } else if (type === 5) {
        // 视频监控，跳转android原生activity
        // let testData1 = {TypeCode: 1, ProjectNO: 1001, DataList: [{EmployeeName: '123', SexText: '232', EventTime: '345', TimeCoutText: '123', Event: '3433', DoorName: '4222', DeptName: 'fsd', JobName: 'skd'}]}
        // var keytest1 = testData1.ProjectNO.toString()
        // testData1.DataList.forEach(element => {
        //   this.$store.getters.doorGuardList[keytest1].unshift(element)
        // })
        window.yushientrypoint = 5
        this.$router.push({ path: '/Videomonitoring' })
      } else if (type === 6) {
        // 安全巡查
        window.yushientrypoint = 6
        this.entryscreen()
      } else if (type === 8) {
        window.yushientrypoint = 8
        var jsonObj = {methodname: 'entryWebScreen', contenturl: 'http://118.178.85.185:8081/hnshgsqms/loginController.do?comelogin&userid=guest&timestamp=1541576350&pwd=C99EC789EB6D4BAA40341591912CB58F'}
        window.plugins.CustomBridgePlugin.js2android(jsonObj, null, null)
      }
    },
    entryscreen () {
      var jsonObj = {methodname: 'entrySecuryScreen', token: this.$store.getters.token, userid: this.$store.getters.code, list: this.$store.getters.projName}
      window.plugins.CustomBridgePlugin.js2android(jsonObj, null, null)
    },
    returnDialogTitle () {
      if (this.$store.getters.dialogIndex === 0) {
        this.dialogTitle = '账户信息'
      } else if (this.$store.getters.dialogIndex === 1) {
        this.dialogTitle = '修改密码'
      } else {
        this.dialogTitle = '更换手机'
      }
      return this.dialogTitle
    },
    handleClickOutside () {
      this.$store.dispatch('closeSideBar', { withoutAnimation: false })
    }
  }
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
  @import "src/styles/mixin.scss";
  .app-wrapper {
      @include clearfix;
      position: relative;
      height: 100%;
      width: 100%;
  }
  .title-image {
    float: left;
    margin-top: 2%;
  }
  .classtitle {
    margin-left: 5px;
    margin-top: 10px;
    float: left;
    width: 80px;
    text-align: left;
    line-height: 1.0;
  }
  .drawer-bg {
      background: #000;
      opacity: 0.3;
      width: 100%;
      top: 0;
      height: 100%;
      position: absolute;
      z-index: 999;
  }
</style>
<style scoped>
 .box-card >>> .el-card__body {
    padding: 10px;
    text-align: center;
  }
</style>



